-->
<template>
  <div class="product">
    <div class="leftTabs">
      <el-menu :default-active="active"  class="el-menu-vertical-demo"  @select="handleSelect">
      <el-menu-item index="1">
        <i class="el-icon-menu"></i>
        <span slot="title">固定式场景</span>
      </el-menu-item>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title">移动式场景</span>
      </el-menu-item>
      <el-menu-item index="3">
        <i class="el-icon-document"></i>
        <span slot="title">对抗性场景</span>
      </el-menu-item>
    </el-menu>
    </div>
    <div class="rightContent">
      <div v-if="active == '1'">
        <img :src="src1" />
      </div>
      <div v-else-if="active == '2'">
        <img :src="src2" />
      </div>
      <div v-else-if="active == '3'">
         <img :src="src3" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: '1',
      src1: require('../assets/img/product1.png'),
      src2: require('../assets/img/product2.png'),
      src3: require('../assets/img/product3.png')
    }
  },
  watch: {
    $route(to){
      window.console.log(to)
      if (to.name == 'plan') {
        this.active = to.params.id
      }
      
    }
  },
  mounted() {
    window.console.log(this.$route.params.id)
    this.active = this.$route.params.id
  },
  methods: {
    handleSelect(key) {
      this.$router.push({name:'plan',params: {id:key}})
    },
  }
};
</script>

<style lang="scss" scoped>
.product{
  overflow: hidden;
  width: 100%;
  height: 100vh;
}
.leftTabs{
  float: left;
  width: 30%;
  ul{
    margin-left: 50%;
    width: 50%;
    text-align: left;
  }
  .el-menu {
    border: none !important;
  }
}
.rightContent{
  float: right;
  width: 70%;
  div {
    width: 100%;
    img{
      width: 100%;
      height: 100%;
    }
  }
}
</style>